import { createGlobalStyle } from "styled-components";

export const GlobalStyles = createGlobalStyle`
  input,
  textarea,
  select,
  a,
  p,
  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    margin: 0;
    text-decoration: none;
    outline: none;
    word-break: break-word;
    overflow-wrap: break-word;
  }

  input:focus,
  textarea:focus,
  select:focus {
    outline: none;
    box-shadow: none;
    border-color: inherit;
  }
    
  @font-face {
    font-family: 'Blair ITC';
    src: url('/fonts/BlairITC-Medium.eot');
    src: url('/fonts/BlairITC-Medium.eot?#iefix') format('embedded-opentype'),
         url('/fonts/BlairITC-Medium.woff2') format('woff2'),
         url('/fonts/BlairITC-Medium.woff') format('woff'),
         url('/fonts/BlairITC-Medium.ttf') format('truetype'),
         url('/fonts/BlairITC-Medium.svg#BlairITC-Medium') format('svg');
    font-weight: 500;
    font-style: normal;
    font-display: swap;
  }

  @font-face {
    font-family: 'Mercury Display';
    src: url('/fonts/MercuryDisplay-Roman.eot');
    src: url('/fonts/MercuryDisplay-Roman.eot?#iefix') format('embedded-opentype'),
         url('/fonts/MercuryDisplay-Roman.woff2') format('woff2'),
         url('/fonts/MercuryDisplay-Roman.woff') format('woff'),
         url('/fonts/MercuryDisplay-Roman.ttf') format('truetype'),
         url('/fonts/MercuryDisplay-Roman.svg#MercuryDisplay-Roman') format('svg');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
  }

  :root {
    --primary-color: #0086BF;
    --secondary-color: #C8102E;
    --font-base: 'Mercury Display', serif;
    --blair-font: 'Blair ITC', sans-serif;
  }

  * {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }

  body {
    font-family: var(--blair-font);
    margin: 0;
    padding: 0;
  }
`;
